* {
  box-sizing: border-box;
}




/* General styles */
body {
  font-size: 16px; /* Increase the base font size for better readability */
  font-family: Arial, sans-serif; /* Change font-family for better cross-browser consistency */
  margin: 0;
  background-color: #f5f5f5; /* Add a light background color */
}

/* Top navigation */
nav {
  background-color: #4d5791;
  height: 60px; /* Increase height for better visibility */
  padding: 0 20px; /* Reduce padding for a more compact header */
}

nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

nav ul li {
  display: inline-block;
}

nav ul li a {
  display: block;
  padding: 20px;
  line-height: 20px; /* Adjust line-height for better alignment */
  font-size: 1.2em; /* Reduce font size slightly */
  text-decoration: none;
  color: #fff;
}

nav ul li a:hover {
  background-color: #fff;
  color: #4d5791;
}

/* Hero section */
div.hero {
  height: 400px; /* Increase height for a more prominent hero section */
  background-image: url('../public/bg.png');
  background-size: cover; /* Use cover to ensure background image fills the container */
  background-position: center;
  padding-top: 80px; /* Increase padding for better spacing */
  text-align: center; /* Center align text */
}

div.hero h1 {
  color: #1229b0; /* Change text color for better contrast */
  font-size: 3.5em; /* Reduce font size slightly */
}

div.hero h2 {
  color: #777; /* Adjust subheading color for better contrast */
}

/* Main content area */
.container {
  padding: 40px; /* Increase padding for better spacing */
  max-width: 1200px; /* Limit container width for better readability */
  margin: 0 auto; /* Center align container */
}

.container p {
  text-align: justify; /* Justify text for better readability */
}

.page-area {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; /* Ensure even spacing between articles */
}

.page-area > article {
  width: calc(50% - 20px);
  margin-bottom: 40px; /* Add margin at the bottom of articles */
}

/* Footer */
.bottom {
  background-color: #4d5791; /* Match footer background color with header */
  color: #fff; /* Adjust text color for better contrast */
  text-align: center;
  padding:20px;
  // position: absolute;
  width: 100%;
  bottom: 0;
}
